import { BatchSpanProcessor, WebTracerProvider } from "@opentelemetry/sdk-trace-web";
import { ZoneContextManager } from "@opentelemetry/context-zone";
import { registerInstrumentations } from "@opentelemetry/instrumentation";
import { OTLPTraceExporter } from "@opentelemetry/exporter-trace-otlp-http";
import { ATTR_SERVICE_NAME, ATTR_SERVICE_VERSION } from "@opentelemetry/semantic-conventions";
import { ATTR_DEPLOYMENT_ENVIRONMENT_NAME } from "@opentelemetry/semantic-conventions/incubating";
import { getWebAutoInstrumentations } from "@opentelemetry/auto-instrumentations-web";

// Check if running in browser environment
const isBrowser = typeof window !== 'undefined';

// Get configuration from window object if we're in a browser (injected by Hugo template)
let authToken = '';
let environment = 'production';

if (isBrowser && window.DASH0_CONFIG) {
  authToken = window.DASH0_CONFIG.authToken;
  environment = window.DASH0_CONFIG.environment || 'production';
  
  // Don't initialize if using the placeholder token
  if (authToken === 'YOUR_DASH0_AUTH_TOKEN') {
    console.warn('Skipping OpenTelemetry initialization: No valid Dash0 auth token provided');
    console.warn('Set HUGO_DASH0_AUTH_TOKEN environment variable in your Cloudflare Pages environment');
  }
}

// Only proceed if we have a valid token
if (authToken && authToken !== 'YOUR_DASH0_AUTH_TOKEN') {
  console.log('Initializing OpenTelemetry for', environment);

  const tracerProvider = new WebTracerProvider({
    resource: {
      attributes: {
        [ATTR_DEPLOYMENT_ENVIRONMENT_NAME]: environment,
        [ATTR_SERVICE_NAME]: "chrisboyd-blog",
        [ATTR_SERVICE_VERSION]: "1.0.0",
        // Add Cloudflare-specific attributes if available
        ...(isBrowser && window.location.hostname.includes('.pages.dev') ? {
          'cf.pages.url': window.location.origin,
          'cf.pages.environment': 'preview'
        } : {})
      },
    },
    spanProcessors: [
      new BatchSpanProcessor(
        new OTLPTraceExporter({
          url: "https://ingress.us-west-2.aws.dash0.com/v1/traces",
          headers: {
            'Authorization': `Bearer ${authToken}`,
          },
        }),
        {
          maxQueueSize: 100, // The maximum queue size. After the size is reached, spans are dropped.
          maxExportBatchSize: 10, // The maximum batch size of every export. It must be smaller or equal to maxQueueSize.
          scheduledDelayMillis: 500, // The interval between two consecutive exports
          exportTimeoutMillis: 30000, // How long the export can run before it is cancelled
        }
      ),
    ],
  });

  tracerProvider.register({
    // Changing default context manager to use ZoneContextManager. This one supports tracking of asynchronous operations.
    // Optional, but recommended for better correlation.
    contextManager: new ZoneContextManager(),
  });

  // Registering instrumentations
  registerInstrumentations({
    instrumentations: [
      // You can configure all the auto-instrumentations via this function's parameter.
      // Learn more via this documentation: https://www.npmjs.com/package/@opentelemetry/auto-instrumentations-web
      getWebAutoInstrumentations({
        // Configure specific instrumentations
        '@opentelemetry/instrumentation-document-load': {
          enabled: true,
        },
        '@opentelemetry/instrumentation-user-interaction': {
          enabled: true,
          eventNames: ['click', 'submit', 'scroll'],
        },
        '@opentelemetry/instrumentation-fetch': {
          enabled: true,
          clearTimingResources: true,
          propagateTraceHeaderCorsUrls: [
            'https://dash0.com',
            'https://ingress.us-west-2.aws.dash0.com'
          ]
        },
        '@opentelemetry/instrumentation-xml-http-request': {
          enabled: true,
          clearTimingResources: true,
          propagateTraceHeaderCorsUrls: [
            'https://dash0.com',
            'https://ingress.us-west-2.aws.dash0.com'
          ]
        },
      }),
    ],
  });
  
  console.log('OpenTelemetry initialized successfully');
} else if (isBrowser) {
  console.warn('OpenTelemetry not initialized: Valid auth token not provided');
} 